
        import {store, MERGED_BUNDLES_NAME} from 'bundleStore';
        
        let bundle;
        
        if ('CommonBundle' !== MERGED_BUNDLES_NAME) {
            store['CommonBundle'] = store['CommonBundle'] || {};
            bundle = store['CommonBundle'];
        } else {
            bundle = Object.keys(store).reduce((acc, key) => {
                return {
                    ...acc,
                    ...store[key]
                }
            }, {});
        }
                                     
        export default bundle;
    